.container-delivery{
  .delivery-options{
    display: flex;
    gap:15px;

    .delivery-option{
      flex:0 0 calc(50% - 7.5px);
      text-align: center;
      padding: 25px 15px;
      border: 1px solid #b925902e;
      border-radius: 10px;

      p{
        margin-bottom: 0;
      }
      
      &.active{
        background-color: #b92590;

        h6, p{
          color: white;
        }
      }
    }

    .delivery-option h6{
      margin: 0;
    }
  }
}