.shipping-accordion{
  .shipping-accordion-list{
    margin-bottom: 5px;

    .shipping-accordion-header{
      background: #b92590;
      color: white;
      padding: 10px 15px;
      font-size: 14px;
      border-radius: 5px;
    }
    .shipping-accordion-content{
      padding: 15px;
      font-size: 14px;

      .form-check{
        margin-bottom: 5px;
      }

      .form-check-label{
        font-size: 12px;
      }
    }
  }
}